import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  display: flex;
  gap: 20px;
  height: 55px;
  align-items: center;
  justify-content: center;
  flex-direction: row;
`

const NavWrapper = styled.nav`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
`;

const NavItem = styled.a`
  padding: 10px;
  color: #000000;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;

  &:not(:last-child) {
    margin-right: 50px;
  }

  /*      */

  &:hover {
    color: ${(props) => props.hoverBackgroundColor || "#a4b2fe"};
    /*       */
  }

\`                   ;
`;

function NavigationBar() {
    return (
        <Wrapper>
            <NavWrapper>
                <NavItem href="/bookbytype"></NavItem>
                <NavItem href="/facility-reservation"></NavItem>
                <NavItem href="/notice"></NavItem>
                <NavItem href="/mypage">MyPage</NavItem>
            </NavWrapper>
        </Wrapper>
    );
}

export default NavigationBar;
